html {
  font-size: 10px;
  scroll-behavior: smooth;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
body {
  overflow-x: hidden;
  &.scroll-lock {
    overflow-y: hidden;
  }
}
*, *::after, *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}
.wrapper {
  overflow: hidden;
  width: 100%;
}
.container {
  margin: auto;
  padding-bottom: 3rem;
  width: 100%;
  max-width: 1170px;
  @include breakpoint($medium-bp) {
    padding: 5rem 0;
  }
}
.content {
  padding: 0 2rem
}
.main-header {
  font: 2.6rem "Lato", sans-serif;
  text-align: center;
  font-weight: bold;
  padding: 5rem 1rem 3rem;
  &--space {
    padding-top: 10rem;
  }
  @include breakpoint($tabled-bp) {
    padding: 10rem 1rem;
  }
  @include breakpoint($medium-bp) {
    padding: 5rem 1rem;
  }
  @include breakpoint($pc-bp) {
    padding: 10rem 1rem;
    font-size: 3.6rem;
  }
}
.sub-header {
  font-size: 1.5rem;
  font-weight: normal;
  text-align: center;
  padding-bottom: 2rem;
}
.header-image {
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    display: block;
    width: 100%;
    max-width: 400px;
  }
}
.highlited-text {
  color: $primary;
  font-weight: 600;
}
.section--black {
  background-color: $dark;
  color: white;
}
.section--grey {
  background-color: $grey-for-background;
}
.section__darken{
  height: 100%;
  backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, .8);
  &--60 {
    background-color: rgba(0, 0, 0, .6);
  }
  &--grey {
    background-color: rgba(31, 31, 31, .6);
  }
}
.to-animate {
  opacity: 0;
}

:root
{
	--duration: 1.5s;
	--container-size: 250px;
	--box-size: 33px;
	--box-border-radius: 15%;
}


@import "media";
